@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700');
@mixin f {
  font-family: 'Open Sans', sans-serif; }

@include font(freestylescript, freestylescript);
@mixin fs {
  font-family: 'freestylescript', serif; }

@include font(champagnelimousinesbold, champagnelimousinesbold);
@mixin fc {
  font-family: 'champagnelimousinesbold', serif; }

$black: #000;
$light-black: #555;
$white: #fff;
$blue: #5b6ceb;
$gray: #888;
$border: #d7d7d7;

// *
//   box-shadow: 0 0 1px red

body {
  min-width: 320px;
  @include f;
  font-size: 14px;
  line-height: 1.3;
  color: $light-black;
  &.no-scroll {
    overflow: hidden; } }

body,
button,
input,
textarea,
select {}

a {
  text-decoration: none;
  color: $light-black; }

button {
  background: none; }

.out {
  padding-top: 100px;
  @include m {
    padding-top: 86px; } }

.center {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px;
  @include t {
    padding: 0 20px; }
  @include m {
    padding: 0 10px; } }

.header {
