      justify-content: space-between;
      min-height: auto;
      padding: 15px 10px; } }
  &__nav {
    display: flex;
    align-items: center;
    width: 100%;
    @include m {
      display: none;
      position: absolute;
      top: 85px;
      left: 0;
      right: 0;
      flex-direction: column;
      padding: 20px;
      background: $white;
      &.visible {
        display: flex; } } }
  &__left,
  &__right {
    flex: 0 0 50%;
    @include m {
      display: flex;
      flex-direction: column;
      flex: 0 0 100%;
      text-align: center; } }
  &__left {
    padding-right: 55px;
    @include m {
      margin-bottom: 10px;
      padding: 0; } }
  &__right {
    padding-left: 55px;
    padding-right: 65px;
    text-align: right;
    @include t {
      padding-right: 40px; }
    @include m {
      padding: 0; } }
  &__item {
    position: relative;
    margin-right: 25px;
    font-weight: 600;
    transition: color .2s;
    @include m {
      margin: 0 0 10px 0; }
    &:last-child {
      margin-right: 0;
      @include m {
        margin: 0; } }
    &:hover {
      color: darken($light-black,50); }
    &.active {
      color: #5363db;
      &:before {
        content: "";
        position: absolute;
        bottom: -5px;
        left: 50%;
        margin-left: -15px;
