      width: 10px;
      height: 10px;
      background: $blue;
      border-radius: 50%; } } }


.owl-carousel {
  .owl-dots {
    display: none; }
  &_comment {
    width: auto;
    .owl-dots {
      display: block;
      @include m {
        display: none; } }
    .owl-prev,
    .owl-next {
      top: auto;
      bottom: 22px;
      width: 29px;
      height: 29px;
      .icon {
        font-size: 12px; } }
    .owl-prev {
      left: 332px;
      @include t {
        left: 350px; }
      @include m {
        left: 85px; } }
    .owl-next {
      left: 580px;
      @include t {
        left: 540px; }
      @include m {
        left: 245px; } } } }

.footer {
  &__body {
    padding: 98px 0 100px;
    background: $blue;
    color: $white;
    @include t {
      padding: 80px 0; }
    @include m {
      padding: 50px 20px; } }
  &__row {
    display: flex;
    margin: 0 -15px 50px;
    @include m {
      margin: 0 0 50px;
      flex-wrap: wrap; } }
  &__col {
    flex: 0 0 calc(50% - 30px);
    margin: 0 15px;
    @include m {
      flex: 0 0 100%;
      margin: 0 0 30px;
      &:last-child {
        margin: 0; } } }
  &__line {
    display: flex;
    margin: 0 -15px 30px;
    @include t {
      margin-bottom: 0 -15px;
      flex-direction: column; }
    &:last-child {
      margin-bottom: 0; } }
  &__cell {
    flex: 0 0 calc(50% - 30px);
    margin: 0 15px;
    @include t {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0; } } }
  &__title {
    margin-bottom: 50px;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    @include m {
      margin-bottom: 15px; } }
  &__text {
    max-width: 465px;
    line-height: (22/14);
    @include m {
      max-width: 100%; } }
  &__item {
    display: flex;
    align-items: center; }
  &__icon {
    flex: 0 0 25px;
    width: 25px;
    font-size: 0;
    .icon {
      font-size: 21px;
      fill: $white; }
    .icon-mail {
      font-size: 13px; } }
  &__info {
    flex: 0 0 calc(100% - 25px);
    padding-left: 10px; }
  &__link {
    display: block;
    color: $white;
    transition: color .2s;
    &:hover {
      color: darken($white,20); } }
  &__copyright {
    padding: 40px 15px;
    background: #5363D8;
    text-align: center;
    font-size: 16px;
    color: #b7bffc;
    @include t {
      padding: 30px 0; } } }


.social {
  display: flex;
  align-items: center;
  &__link {
    margin-right: 19px;
    font-size: 0;
    .icon {
      font-size: 21px;
      fill: $white;
      transition: fill .2s; }
    &:last-child {
      margin-right: 0; }
    &:hover {
      .icon {
        fill: darken($white,20); } } } }

.anchor {
  position: absolute;
  left: 0; }














